.form-input {
  position: relative;
  bottom: -$height-form-input/2;
  left: 0;
  width: 100%;
  min-height: $height-form-input;
  background-color: $secondary-color;
  background-image: url(../images/bg-shorten-desktop.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 8px;
  margin-bottom: 18px;

  &__wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 100%;
    padding: 0 64px;
  }

  &__inner {
    position: relative;
    width: 100%;
    margin-right: 12px;
  }

  &__input {
    width: 100%;
    outline: none;
    padding: 18px;
    flex-grow: 1;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;

    &::placeholder {
      color: $gray-violet-color;
    }

    &.error {
      border: 3px solid $secondary-red-color;
      &::placeholder {
        color: $secondary-red-color;
      }
    }
  }

  &__btn {
    font-size: 1.8rem;
    padding: 17px 70px;
    border-radius: 8px;
  }

  .error-message {
    position: relative;
    top: 6px;
    left: 0;
    white-space: nowrap;
    color: $secondary-red-color;
    font-size: 1.2rem;
    font-style: italic;
    margin: 0;
    @include text-overflow;
  }
}

@media (max-width: 767px) {
  .form-input {
    $height: 160px;

    &__wrapper {
      padding: 0 24px;
      flex-flow: column wrap;
    }

    &__inner {
      margin-right: 0;
    }

    &__input {
      font-size: 1.7rem;
      margin-right: 0;
      margin-bottom: 12px;
      padding-top: 12px;
      padding-bottom: 12px;
    }

    &__btn {
      font-size: 1.6rem;
      width: 100%;
      padding: 10px 24px;
    }

    .error-message {
      top: -8px;
    }
  }
}
